.alert-shade{
  left:0;
  top:0;
  width:100%;
  height:100%;
  background: #000;
  opacity: 0.3;
   filter: alpha(opacity=30);
  position: fixed;
}
.alert-container{
  display: inline-block;
  max-width: 80vw;
  max-height: 80vh;
  border-radius: 2px;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  -o-border-radius: 2px;
  background: #fff;
  border:1px solid #f1f1f1;
  position: fixed;
  z-index: 99999;
  overflow: hidden;
}
.alert-title{
	margin-bottom: 15px;
	padding: 0 40px 0 20px;
  font-size: 16px;
  text-align: left;
  line-height: 42px;
  color:#333;
  background: #f8f8f8;
}
.alert-content{
	height:calc( 100% - 94px );
	padding:20px;
	text-align: center;
  font-size: 14px;
  color:#555;
  overflow: auto;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
}
.alert-content-scrollbar{

}
.alert-btn-box{
	padding: 0 15px 12px;
  text-align: right;
  background: #fff;
}
.alert-btn-box .alert-btn{
	display: inline-block;
	height: 28px;
	line-height: 28px;
	margin: 5px;
	padding: 0 15px;
	border: 1px solid #dedede;
	background-color: #fff;
	color: #333;
	border-radius: 2px;
	font-weight: 400;
	cursor: pointer;
	text-decoration: none;
}
.alert-btn-box .alert-btn:first-of-type{
	border-color: #1E9FFF;
	background-color: #1E9FFF;
	color: #fff;
}
.alert-btn:hover{
  opacity: .9;
}
.alert-btn:active{
  opacity: .8;
}
.alert-btn-close{
	display: block;
  position: absolute;
  right:10px;
  top:3px;
  font-size: 24px;
  cursor: pointer;
  z-index: 1000;
}
.alert-container-black{
  background-color:rgba(0,0,0,0.65);
  border:none;
  filter:progid:DXImageTransform.Microsoft.gradient(startColorstr=#7f000000,endColorstr=#7f000000);
}
.alert-container-black .alert-content{
    color: #fff;  
}
.linearTop{
  -webkit-animation-name: linearTop;
  animation-name: linearTop;
}
.linearBottom{
  -webkit-animation-name: linearBottom;
  animation-name: linearBottom;
}
@-webkit-keyframes linearBottom {
   0%{
      -webkit-transform: translate3d(0,0,0);
      transform: translate3d(0,0,0);
      opacity: 1;
      filter:alpha(opacity=100);
   }
   100%{
      -webkit-transform: translate3d(0,80px,0);
      transform: translate3d(0,80px,0);
      opacity: 0;
      filter:alpha(opacity=0);
   }
}
@keyframes linearBottom {
   0%{
      -webkit-transform: translate3d(0,0,0);
      transform: translate3d(0,0,0);
      opacity: 1;
      filter:alpha(opacity=100);
   }
   100%{
      -webkit-transform: translate3d(0,80px,0);
      transform: translate3d(0,80px,0);
      opacity: 0;
      filter:alpha(opacity=0);
   }
}
@-webkit-keyframes linearTop {
   0%{
     -webkit-transform: translate3d(0,80px,0);
      transform: translate3d(0,80px,0);
      opacity: 0;
      filter:alpha(opacity=0);
   }
   100%{
       -webkit-transform: translate3d(0,0,0);
      transform: translate3d(0,0,0);
      opacity: 1;
      filter:alpha(opacity=100);
   }
}
@keyframes linearTop {
   0%{
     -webkit-transform: translate3d(0,80px,0);
      transform: translate3d(0,80px,0);
      opacity: 0;
      filter:alpha(opacity=0);
   }
   100%{
       -webkit-transform: translate3d(0,0,0);
      transform: translate3d(0,0,0);
      opacity: 1;
      filter:alpha(opacity=100);
   }
}
/*显示动画*/
.animated.infinite {
  animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite;
  -moz-animation-iteration-count: infinite;
  -o-animation-iteration-count: infinite;
}
.animated.linearBottom,
.animated.linearTop,
.animated.bounceIn,
.animated.bounceOut {
  -webkit-animation-duration: .45s;
  animation-duration: .45s;
}
@-webkit-keyframes bounceIn {
  0%, 20%, 40%, 60%, 80%, 100% {
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
    animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
  }

  0% {
    opacity: 0;
    filter:alpha(opacity=0);
    -webkit-transform: scale3d(.3, .3, .3);
    transform: scale3d(.3, .3, .3);
  }


  100% {
    opacity: 1;
    filter: alpha(opacity=100);
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
}
@keyframes bounceIn {
  0%, 20%, 40%, 60%, 80%, 100% {
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
    animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
  }

  0% {
    opacity: 0;
    filter:alpha(opacity=0);
    -webkit-transform: scale3d(.3, .3, .3);
    transform: scale3d(.3, .3, .3);
  }


  100% {
    opacity: 1;
    filter: alpha(opacity=100);
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
}
.bounceIn {
  -webkit-animation-name: bounceIn;
  animation-name: bounceIn;
}
@-webkit-keyframes bounceOut {
  20% {
    -webkit-transform: scale3d(1.2, 1.2, 1.2);
    transform: scale3d(1.2, 1.2, 1.2);
  }

  65%, 70% {
    opacity: 1;
    filter: alpha(opacity=100);
    -webkit-transform: scale3d(.75, .75, .75);
    transform: scale3d(.75, .75, .75);
  }

  100% {
    opacity: 0;
     filter: alpha(opacity=0);
    -webkit-transform: scale3d(.3, .3, .3);
    transform: scale3d(.3, .3, .3);
  }
}
@keyframes bounceOut {
  20% {
    -webkit-transform: scale3d(1.2, 1.2, 1.2);
    transform: scale3d(1.2, 1.2, 1.2);
  }

  65%, 70% {
    opacity: 1;
    filter: alpha(opacity=100);
    -webkit-transform: scale3d(.75, .75, .75);
    transform: scale3d(.75, .75, .75);
  }

  100% {
    opacity: 0;
     filter: alpha(opacity=0);
    -webkit-transform: scale3d(.3, .3, .3);
    transform: scale3d(.3, .3, .3);
  }
}
.bounceOut {
  -webkit-animation-name: bounceOut;
  animation-name: bounceOut;
