	position: fixed;
	top: 0;
	left: 0;
	z-index: 999999;
	background-color: rgba(0,0,0,.6);
	background-image: url("data:image/gif;base64,");
	background-repeat: no-repeat;
	background-position: center;
}
.mixin-loading2{
	background-color: transparent;
	background-image: url("data:image/gif;base64,");
}
.mixin-tips-container{
	display: none;
	padding: 5px;
    visibility: visible;
    font-size: 12px;
    line-height: 1.5;
    position: absolute;
    z-index: 1060;
}
.mixin-tips-content{
	max-width: 250px;
    padding: 8px 12px;
    color: #fff;
    text-align: left;
    text-decoration: none;
    background-color: rgba(70,76,91,.9);
    border-radius: 4px;
    -webkit-box-shadow: 0 1px 6px rgba(0,0,0,.2);
            box-shadow: 0 1px 6px rgba(0,0,0,.2);
    white-space: nowrap;
}
.mixin-tips-container:after{
	content: '';
	position: absolute;
    width: 0;
    height: 0;
    border-color: transparent;
    border-style: solid;
    
}
.mixin-tips-container:before{
	content: "";
	display: none;
    width: 0;
    height: 0;
    position: absolute;
    border: 7px solid transparent;
    z-index: 9;
}
.arrow-top:after{
	bottom: 0px;
    border-width: 5px 5px 0;
    border-top-color: rgba(70,76,91,.9);
    left: 50%;
    margin-left: -5px;
}
.arrow-left:after{
	right: 0;
    border-width: 5px 0 5px 5px;
    border-left-color: rgba(70,76,91,.9);
    top: 50%;
    margin-top: -5px;
}
.arrow-right:after{
	left: 0;
    border-width: 5px 5px 5px 0;
    border-right-color: rgba(70,76,91,.9);
    top: 50%;
    margin-top: -5px;
}
.arrow-bottom:after{
	top: 0;
    border-width: 0 5px 5px;
    border-bottom-color: rgba(70,76,91,.9);
    left: 50%;
    margin-left: -5px;
}
.light .mixin-tips-content{background-color: #fff;color: #515a6e;}
.light.arrow-bottom:after{
	top: -3px;
	margin-left: -7px;
	border-width: 0 7px 7px;
	border-bottom-color: hsla(0,0%,85%,.5);
}
